<template>
  <div>
    <button class="delete btn btn-dark" @click="delAll">清空</button>
    <phone-list
      :list="oldlist"
      :isPhone="isPhone"
      @swipeleft="recover"
      @swiperight="delItem"
    />
    <computer-list
      :list="oldlist"
      :isPc="!isPhone"
    >
      <template v-slot:item="{ item }">
        <a class="del" @click.prevent="delItem(item.id)">刪除/</a>
        <a class="recover" @click.prevent="recover(item.id)">恢复</a>
      </template>
    </computer-list>
    <div class="footer">
      <span>总共完成{{ oldlist.length }}件事</span>
    </div>
  </div>
</template>

<script>
import ComputerList from '@/components/ComputerList.vue'
import PhoneList from '@/components/PhoneList.vue'
export default {
  data () {
    return {
      oldlist: []
    }
  },

  components: {
    ComputerList,
    PhoneList
  },
  created () {
    this.ScreenWidth = document.body.clientWidth
  },
  mounted () {
    var old = JSON.parse(localStorage.getItem('done'))
    if (old) {
      this.oldlist = old
    }
  },
  computed: {
    isPhone () {
      if (this.ScreenWidth <= 500) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    delAll () {
      this.oldlist.splice(0)
      localStorage.setItem('done', JSON.stringify(this.oldlist))// 保存更改
    },
    delItem (id) {
      for (var i in this.oldlist) {
        if (this.oldlist[i].id === id) {
          var delId = i
        }
      }
      this.oldlist.splice(delId, 1)
      localStorage.setItem('done', JSON.stringify(this.oldlist))
    },
    recover (id) {
      for (var i in this.oldlist) {
        if (this.oldlist[i].id === id) {
          var recoverId = i
        }
      }
      var keyWord = this.oldlist[recoverId]
      try {
        var reDo = JSON.parse(localStorage.getItem('todo'))
        reDo.push(keyWord)
        localStorage.setItem('todo', JSON.stringify(reDo))
      } catch (error) {
        localStorage.setItem('todo', keyWord)
      }
      this.oldlist.splice(recoverId, 1)
      localStorage.setItem('done', JSON.stringify(this.oldlist))
    }
  }
}

</script>
