button{
  margin-bottom: 20px;
  margin-top: -40px;
}
ul li{
  & a{
    text-decoration: none;
    color: #fff;
    cursor: pointer;
    transition: color 0.5s;
  }
  &:hover{
    .del{
      color: rgb(134, 23, 23);
    }
    .recover{
      color: rgb(18, 114, 18);
    }
  }
}
.footer {
  color: #777;
  width: 90%;
  height: 20px;
  text-align: center;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
  border-top: 1px solid #e6e6e6;
  & span{
    margin: auto;
    vertical-align: baseline;
  }
}
.delete {
  &:hover {
    background-image:
    gradient(linear,left top,left bottom,from(#343a40),to(#6e6d6d));
    animation-timing-function:ease-in-out;
    animation-name:breathe;
    animation-duration:1700ms;
    animation-iteration-count:infinite;
    animation-direction:alternate;
  }
}
@keyframes breathe {
  0% {
    opacity:.6;
    box-shadow:0 1px 2px #282d31;
  }
  100% {
    opacity:1;
    box-shadow:0 1px 20px rgb(75, 75, 75);
  }
}
</style>
